am5.ready(function() {
  var data = [
    { id: "NA", name: "North America", value: 42, latitude: 54.5260, longitude: -105.2551 },
    { id: "SA", name: "South America", value: 3, latitude: -8.7832, longitude: -55.4915 },
    { id: "AF", name: "Africa", value: 2, latitude: 1.6508, longitude: 17.6791 },
    { id: "AS", name: "Asia", value: 20, latitude: 34.0479, longitude: 100.6197 },
    { id: "OC", name: "Oceania", value: 2, latitude: -22.7359, longitude: 140.0188 },
    { id: "EU", name: "Europe", value: 32, latitude: 54.5260, longitude: 15.2551 }
  ];

  var root = am5.Root.new("websiteMap");
  root.setThemes([am5themes_Animated.new(root)]);

  var chart = root.container.children.push(am5map.MapChart.new(root, {
    panX: "none",
    panY: "none",
    wheelX: "none",
    wheelY: "none"
  }));

  var polygonSeries = chart.series.push(
    am5map.MapPolygonSeries.new(root, {
      geoJSON: am5geodata_worldLow,
      exclude: ["AQ"]
    })
  );

  var bubbleSeries = chart.series.push(
    am5map.MapPointSeries.new(root, {
      valueField: "value",
      longitudeField: "longitude",
      latitudeField: "latitude"
    })
  );

  var circleTemplate = am5.Template.new({});

  bubbleSeries.bullets.push(function(root, series, dataItem) {
    var container = am5.Container.new(root, {});

    var circle = container.children.push(
      am5.Circle.new(root, {
        radius: 20,
        fillOpacity: 0.9,
        fill: am5.color(0xdc3545),
        cursorOverStyle: "pointer",
        tooltipText: `{name}: [bold]{value}%[/]`
      }, circleTemplate)
    );

    var countryLabel = container.children.push(
      am5.Label.new(root, {
        text: "{name}",
        paddingTop: 20,
        populateText: true,
        fontWeight: "bold",
        fontSize: 16,
        centerX: am5.p50
      })
    );

    circle.on("radius", function(radius) {
      countryLabel.set("x", radius);
    });

    return am5.Bullet.new(root, {
      sprite: container,
      dynamic: true
    });
  });

  bubbleSeries.bullets.push(function(root, series, dataItem) {
    return am5.Bullet.new(root, {
      sprite: am5.Label.new(root, {
        text: "{value.formatNumber('#.')}%",
        fill: am5.color(0xffffff),
        populateText: true,
        centerX: am5.p50,
        centerY: am5.p50,
        textAlign: "center"
      }),
      dynamic: true
    });
  });

  // bubbleSeries.set("heatRules", [
  //   {
  //     target: circleTemplate,
  //     dataField: "value",
  //     min: 10,
  //     max: 50,
  //     minValue: 0,
  //     maxValue: 100,
  //     key: "radius"
  //   }
  // ]);

  bubbleSeries.data.setAll(data);


});
