am5.ready(function() {
  var root = am5.Root.new("audienceMap");

  root.setThemes([
    am5themes_Animated.new(root)
  ]);

  var map = root.container.children.push(
    am5map.MapChart.new(root, {
      panX: "none",
      panY: "none",
      wheelX: "none",
      wheelY: "none",
      projection: am5map.geoNaturalEarth1()
    })
  );

  var polygonSeries = map.series.push(
    am5map.MapPolygonSeries.new(root, {
      geoJSON: am5geodata_continentsLow,
      exclude: ["antarctica"],
      fill: am5.color(0xbbbbbb)
    })
  );

  var pointSeries = map.series.push(
    am5map.MapPointSeries.new(root, {})
  );

  var colorSet = am5.ColorSet.new(root, { step: 2 });

  pointSeries.bullets.push(function(root, series, dataItem) {
    var value = dataItem.dataContext.value;
    var container = am5.Container.new(root, {});
    var color = colorSet.next();
    var radius = 20;  // Фіксований радіус для всіх кружків

    // Кружок із тултіпом
    var circle = container.children.push(am5.Circle.new(root, {
      radius: radius,
      fill: color,
      dy: -radius * 2,
      tooltipText: "{title}",
      cursorOverStyle: "pointer"
    }));

    container.children.push(am5.Line.new(root, {
      stroke: color,
      height: -40,
      strokeGradient: am5.LinearGradient.new(root, {
        stops: [
          { opacity: 1 },
          { opacity: 1 },
          { opacity: 0 }
        ]
      })
    }));

    container.children.push(am5.Label.new(root, {
      text: String(value),
      fill: am5.color(0xffffff),
      fontWeight: "400",
      fontSize: 14,
      centerX: am5.p50,
      centerY: am5.p50,
      dy: -radius * 2
    }));

    return am5.Bullet.new(root, {
      sprite: container
    });
  });

  var data = [
    { title: "Canada / Mexico / Caribbean", latitude: 55.0, longitude: -110.0, value: 1924 },
    { title: "Pacific / West Coast", latitude: 37.0, longitude: -120.0, value: 4233 },
    { title: "Gulf Coast", latitude: 30.0, longitude: -90.0, value: 10784 },
    { title: "South / Central America", latitude: -10.0, longitude: -60.0, value: 456 },
    { title: "Lakes / Inland", latitude: 54.0, longitude: -84.0, value: 5523 },
    { title: "East Coast", latitude: 38.0, longitude: -65.0, value: 8180 },
    { title: "Europe", latitude: 50.896104, longitude: 19.160156, value: 6808 },
    { title: "Africa / Middle East", latitude: 11.081385, longitude: 21.621094, value: 1081 },
    { title: "Asia / Pacific / Oceania", latitude: 20.0, longitude: 100.0, value: 3042 }
  ];

  data.forEach(function(d) {
    pointSeries.data.push({
      geometry: { type: "Point", coordinates: [d.longitude, d.latitude] },
      title: d.title,
      value: d.value
    });
  });

});
